import React, { useEffect } from 'react';

interface SEOHeadProps {
  title?: string;
  description?: string;
  keywords?: string;
  image?: string;
  url?: string;
  type?: 'website' | 'article' | 'product';
  gameName?: string;
  cheatName?: string;
  structuredData?: any;
}

export const SEOHead: React.FC<SEOHeadProps> = ({
  title = 'SoftPack -    ',
  description = 'SoftPack -       . CS:GO, Valorant, GTA V  .     .',
  keywords = ',   , CS:GO , Valorant , GTA V ,  ,  ,   ',
  image = 'https://i.imgur.com/0y0y0y0.png',
  url = 'https://softpack.ru',
  type = 'website',
  gameName,
  cheatName,
  structuredData
}) => {
  const fullTitle = cheatName 
    ? `${cheatName}  ${gameName} -   | SoftPack`
    : gameName 
    ? `  ${gameName} -  | SoftPack`
    : title;

  const fullDescription = cheatName
    ? ` ${cheatName}  ${gameName}. ${description}`
    : gameName
    ? `   ${gameName}. ${description}`
    : description;

  const fullKeywords = cheatName
    ? `${cheatName}, ${gameName}, , , ${keywords}`
    : gameName
    ? `${gameName}, , , ${keywords}`
    : keywords;

  useEffect(() => {
    //  title
    document.title = fullTitle;

    //  -
    const updateMetaTag = (name: string, content: string, property?: boolean) => {
      const selector = property ? `meta[property="${name}"]` : `meta[name="${name}"]`;
      let meta = document.querySelector(selector) as HTMLMetaElement;
      
      if (!meta) {
        meta = document.createElement('meta');
        if (property) {
          meta.setAttribute('property', name);
        } else {
          meta.setAttribute('name', name);
        }
        document.head.appendChild(meta);
      }
      meta.setAttribute('content', content);
    };

    //  -
    updateMetaTag('description', fullDescription);
    updateMetaTag('keywords', fullKeywords);
    updateMetaTag('author', 'SoftPack Team');
    updateMetaTag('robots', 'index, follow');
    updateMetaTag('viewport', 'width=device-width, initial-scale=1.0');
    updateMetaTag('theme-color', '#7c9cff');
    updateMetaTag('msapplication-TileColor', '#7c9cff');
    updateMetaTag('apple-mobile-web-app-capable', 'yes');
    updateMetaTag('apple-mobile-web-app-status-bar-style', 'black-translucent');

    // Open Graph -
    updateMetaTag('og:type', type, true);
    updateMetaTag('og:title', fullTitle, true);
    updateMetaTag('og:description', fullDescription, true);
    updateMetaTag('og:image', image, true);
    updateMetaTag('og:url', url, true);
    updateMetaTag('og:site_name', 'SoftPack', true);
    updateMetaTag('og:locale', 'ru_RU', true);

    // Twitter Card -
    updateMetaTag('twitter:card', 'summary_large_image');
    updateMetaTag('twitter:title', fullTitle);
    updateMetaTag('twitter:description', fullDescription);
    updateMetaTag('twitter:image', image);
    updateMetaTag('twitter:site', '@softpack');
    updateMetaTag('twitter:creator', '@softpack');

    // Canonical URL
    let canonical = document.querySelector('link[rel="canonical"]') as HTMLLinkElement;
    if (!canonical) {
      canonical = document.createElement('link');
      canonical.setAttribute('rel', 'canonical');
      document.head.appendChild(canonical);
    }
    canonical.setAttribute('href', url);

    //   JSON-LD
    if (structuredData) {
      //   JSON-LD 
      const oldScripts = document.querySelectorAll('script[type="application/ld+json"]');
      oldScripts.forEach(script => script.remove());

      //  
      const script = document.createElement('script');
      script.type = 'application/ld+json';
      script.textContent = JSON.stringify(structuredData);
      document.head.appendChild(script);
    }
  }, [fullTitle, fullDescription, fullKeywords, image, url, type, structuredData]);

  return null; //      
};

//     
export const SEOConfigs = {
  home: {
    title: 'SoftPack -    ',
    description: 'SoftPack -       . CS:GO, Valorant, GTA V  .     .',
    keywords: ',   , CS:GO , Valorant , GTA V ,  ,  ,   ',
    type: 'website' as const
  },
  games: {
    title: '  -   | SoftPack',
    description: '    .        CS:GO, Valorant, GTA V    .',
    keywords: ' ,    , CS:GO, Valorant, GTA V, Fortnite,  ',
    type: 'website' as const
  },
  about: {
    title: '  - SoftPack',
    description: '   SoftPack -     .   -     .',
    keywords: ' , SoftPack,  ,  , ',
    type: 'website' as const
  },
  events: {
    title: '   - SoftPack',
    description: '      SoftPack.  ,     .',
    keywords: ', ,  , , SoftPack',
    type: 'website' as const
  },
  admin: {
    title: '- - SoftPack',
    description: '   SoftPack',
    keywords: ', , SoftPack',
    type: 'website' as const
  }
};
